<template >
  <div class="">
    <div class="footer" @click="changeshow">
      <div class="cartout">
        <div class="cartin" :class="{on:foodNum}">
          <span class="icon icon-gouwuche"></span>
          <strong v-show="foodNum">{{foodNum}}</strong>
        </div>
      </div>
      <strong>¥{{foodPrice}}</strong>
      <p>另需配送费¥{{deliveryPrice}}元</p>
      <em :class="{go:account == '去结算'}">{{account}}</em>
    </div>
    <transition name="slideup">
      <div class="foodlist" v-show="showlist">
        <div class="bg"></div>
        <p><span>购物车</span><i @click="empty">清空</i></p>
        <ul>
          <li v-for="food in cartgoods" class="food" >
            <p>{{food.name}}</p>
            <div class="price"><span>¥</span><em>{{food.price}}</em></div>
            <addCart :food="food" @addNum="addNum(food)" @subNum="subNum(food)"></addCart>
          </li>
        </ul>
      </div>
    </transition>

  </div>

</template>

<script>
  import addCart from '../addCart/addCart'
  export default {
    data(){
      return {
        show:false
      }
    },
  props:{
    cartgoods:{
      type:Array,
      require:true
    },
    minPrice:Number,
    deliveryPrice:Number
  },
  computed:{
    foodNum(){
      let num = 0;
      this.cartgoods.forEach( food=>num+=food.num)
      return num
    },
    foodPrice(){
      let price = 0;
      this.cartgoods.forEach( food=>price+= (food.num*food.price));
      return price;
    },
    account(){
      if(this.foodPrice == 0){
        return `¥${this.minPrice}元 起送`;
      }else if(this.foodPrice < this.minPrice){
        let P = this.minPrice -this.foodPrice
        return `还差¥${P}元 起送`;
      }else{
        return '去结算'
      }
    },
    showlist(){
      if(this.foodNum == 0){
        this.show = false;
      }
      return this.show
    }
  },
  components:{
    addCart
  },
  methods:{
    empty(){
      this.cartgoods.forEach( item => item.num = 0)
      this.show = false;
    },
    changeshow(){
      if(this.foodNum){
        this.show = !this.show;
      }

    },
    addNum(food){
      if(food.num){
        food.num++
      }else{
        this.$set(food,'num',1);
      }
    },
    subNum(food){
      food.num--;
    },
  }
}
</script>

<style lang="less" scoped>
  .slideup-enter,.slideup-leave-active{transform: translateY(100%);}
  .slideup-enter-active,.slideup-leave-active{transition: all .5s};
  .footer{
    height: 48px;position: fixed;bottom:0px;background-color: #141d27;
    z-index:100;left:0;right:0;
    color:rgba(255,255,255,.4);display: flex;align-items: center;
    .cartout{width: 58px;height: 58px;z-index: 2;
      position: absolute;;left:18px;bottom:0px;
      background-color: #fff;border-radius: 50%;background-color: #141d27;}
      .cartin{width: 44px;height:44px;background-color:rgba(255,255,255,.4);position: absolute;
      border-radius: 50%;left:6px;top:6px;text-align: center;line-height: 44px;
      &.on{background-color: rgb(0,160,220)}
      &.on span{color:#fff;}
      span{font-size: 24px;}
      strong{width: 24px;height: 18px;background-color: rgb(240,20,20);position: absolute;
      border-radius: 50px;color:#fff;left:30px;top:-8px;font-weight: 700;
      line-height: 18px;text-align: center;padding:0;margin:0;}
    }
    strong{margin-left: 80px;font-size: 16px;font-weight: 700;border-right: 1px solid rgba(255,255,255,.1);
    padding-right:12px;margin-right: 12px;}
    p{font-size: 12px;}
    em{margin-left:auto;width: 105px;height: 100%;background-color:rgba(255,255,255,.4);
      font-size: 12px;font-weight: 700;line-height: 48px;text-align: center;
      &.go{background-color: green;}
    }
  }
  .foodlist{
    .bg{position: fixed;top:0;left:0;bottom:48px;right:0;background-color: rgba(0,0,0,.3);z-index: -1;}
    &>p{background-color: #f3f5f7;height: 40px;display: flex;padding:0 18px;
      align-items: center;justify-content: space-between;
      span{font-size: 14px;font-weight: 200;color:rgb(7,17,27);}
      i{font-size: 12px;color:rgb(0,160,220);}
    }
    position: fixed;bottom:48px;background-color: #fff;left:0;right:0;
    .food{display: flex;justify-content: space-between;background-color: #fff;padding:0 18px;
      align-items: center;height: 48px;position: relative;border-bottom: 1px solid #ccc;
      p{font-size: 14px;color:rgb(7,17,27);}
      .addCart{position: absolute;top:8px;right:18px;}
      .price{
        position: absolute;top:12px;right:100px;
        span{font-size: 10px;color:rgb(240,20,20);}
        em{font-size: 14px;color:rgb(240,20,20);font-weight: 700;}
      }
    }
  }
</style>



