<template>
  <div id="app">
    <ele-header :seller="seller"></ele-header>
    <ul class="nav">
      <li><router-link to="/">商品</router-link></li>
      <li><router-link to="/comments">评论</router-link></li>
      <li><router-link to="/seller">商家</router-link></li>
    </ul>
    <router-view></router-view>
  </div>
</template>

<script>
import Header from './components/Header/Header'
import './assets/css/z.css'
import './assets/css/font.css'
export default {
  name: 'app',
  data(){
    return{
      seller:{}
    }
  },
  created(){
    let ele = require('../data.json');
    this.seller = ele.seller;
  },
  components:{
    "ele-header":Header
  }
}
</script>

<style lang="less" scoped>
  .nav{
    height: 40px;display: flex;justify-content: space-around;align-items: center;
    border-bottom: 1px solid rgba(7,17,27,.1);font-size: 14px;color:rgb(77,85,93);
    line-height:28px;
    a.router-link-exact-active{color:rgb(240,20,20)}
  }
</style>



