<template>
    <div id="iscroll-wrapper">
      <div class="">
        <div class="grade"><!--评分块-->
            <div class="gradeLeft ">
              <h3>{{seller.score}}</h3>
              <p class="multiple">综合评分</p>
              <p class="">高于周边商家69.2%</p>
            </div>
            <ul class="gradeRight">
              <li>
                <strong>服务态度</strong>
                <Start :seller="seller.serviceScore"  class="comments"></Start>
                <em>{{seller.serviceScore}}</em>
              </li>
              <li>
                <strong>食物评分</strong>
                <Start :seller="seller.foodScore" class="comments"></Start>
                <em>{{seller.foodScore}}</em>
              </li>
              <li>
                <strong>送达时间</strong>
                <em>{{seller.deliveryTime}}分钟</em>
              </li>
            </ul>
        </div>
        <div class="evaluate">
          <h3>商品评价</h3>
          <div>
            <p class="all"><span>全部</span> {{allevaluate}}</p>
            <p class="recommend"><span>推荐</span>
              {{recommend}}
            </p>
            <p class="taunt"><span>吐槽</span>
              {{taunt}}
            </p>
        </div>
          <p class="select"><span class="icon icon-gou" @click="all=!all" :class="{only:!all}"></span>只看有内容的评价</p>
        </div>
        <ul class="evaluateMsg">
          <li v-for="(item,index) in showall">
            <div class="time">
              <div class="">
                <img :src="item.avatar" alt="" >
                <div class="user">
                  <span>{{item.username}}</span>
                  <Start :seller="item.score" class="usergrade"></Start>
                </div>
              </div>
              <span class="newstime">{{time}}</span>
            </div>
              <div class="evacontent"> <!--评价内容-->
                <p>{{item.text}}</p>
                <span class="icon"
                :class="{'icon-down':item.rateType == 0,'icon-damuzhi':item.rateType == 1}">
              </span>
                <em v-for="goods in item.recommend ">{{goods}}</em>
              </div>
          </li>
        </ul>
      </div>
    </div>
</template>

<script>
  import Start from '../start/start';
  import ele from '../../../data.json';
  require ('../../assets/js/iscroll-probe.js');
  export default {
    components:{
      Start
    },
    data(){
      return {
        seller:{},
        ratings:[],
        all:true
      }
    },
    created(){
      this.seller = ele.seller;
      this.ratings = ele.ratings;
    },
    computed:{
      allevaluate(){
        // console.log(this.ratings)
        if(this.ratings){
          return this.ratings.length;
        }else{
          return 3;
        }
      },
      recommend(){
        if(this.ratings){
          return this.ratings.filter( msg =>msg.rateType == 1).length;
        }else{
          return 1;
        }
      },
      taunt(){
        if(this.ratings){
          return this.ratings.filter( msg =>msg.rateType == 0).length;
        }else{
          return 2;
        }
      },
      time(){
        return new Date().toLocaleString();
      },
      showall(){
        if(this.all){
          return this.ratings
        }else{
          return this.ratings.filter(food => food.text);
        }
      }
    },
    mounted(){
      new IScroll("#iscroll-wrapper");
    },
    updated(){
      new IScroll("#iscroll-wrapper");
    }
  }
</script>

<style lang="less" scoped>
  @import '../../assets/less/public.less';
  #iscroll-wrapper{
    position: fixed;top:175px;bottom:0;left:0;right:0;
    box-sizing: border-box;width: 100%;overflow: hidden;
  }
  .grade{.padding(36px,0,36px,0);overflow: hidden;
    border-bottom: 1px solid rgba(7,17,27,.1);
    .gradeLeft{border-right: 1px solid rgba(7,17,27,.1);
      .w(275);text-align: center;float: left;box-sizing: border-box;
      h3{.fs(48);color:rgb(255,153,0);.lh(56);.margin(0,0,12,0)}
      .multiple{.fs(24);color:rgb(7,17,27);.lh(24);.margin(0,0,16,0)}
      p:last-child{.fs(20);color:rgb(147,153,159);.lh(20);}
    }
    .gradeRight{
      .padding(0,48,0,48);.w(474);float: left;box-sizing: border-box;
      li{display: flex;flex-wrap: nowrap;
        .comments{.h(48);.margin(0,0,0,20);}
        strong{.fs(24);color:rgb(7,17,27);.lh(36);}
        em{.margin(0,0,0,24);.fs(24);color:rgb(147,153,159);.lh(36);}
      }

    }
  }
  .introduce{padding:18px;border-bottom:1px solid rgba(7,17,27,.1);
      border-top:1px solid rgba(7,17,27,.1);margin-top:16px;background-color: #fff;
      p{line-height: 24px;}
  }
  .evaluate{
    padding:18px;border-bottom:1px solid rgba(7,17,27,.1);
    background-color: #fff;
    div{border-bottom:1px solid rgba(7,17,27,.1); padding-bottom: 18px;padding-top: 18px;}
    .all{height: 29px;width: 50px;color:#fff;line-height: 29px;text-align: center;
      background-color: rgb(0,160,220);display: inline-block;font-size: 8px;
      vertical-align: middle;
      span{font-size: 12px}
    }
      .recommend{height: 29px;width: 50px;color:rgb(77,85,93);line-height: 29px;text-align: center;
        background-color: rgba(0,160,220,.2);display: inline-block;font-size: 8px;
        span{font-size: 12px}
      }
        .taunt{height: 29px;width: 50px;color:rgb(77,85,93);line-height: 29px;text-align: center;
              background-color: rgba(77,85,93,.2);display: inline-block;font-size: 8px;
              span{font-size: 12px}
          }
      .select{margin-top: 12px;color:rgb(147,153,159);
        .icon{margin-right: 4px;} .icon.only{color:rgb(0,160,220);}
      }
  }
  .evaluateMsg{
    padding:0 18px;background-color: #fff;
    li{border-bottom:1px solid rgba(7,17,27,.1);
      img{float:left;.margin(36,24,0,0);
        display: inline-block;.w(56);.h(56);border-radius: 50%;}
        .time{
          display: flex;justify-content: space-between;margin-bottom: 12px;
          align-items: center;
          .user{
            float:left;.margin(36,0,0,0);
            span{.fs(10);.lh(24);color:rgb(7,17,27);}
          }
        }
        .evacontent{
          .margin(0,0,0,80);
          p{.fs(24);.lh(36);color:rgb(7,17,27);}
          em{.padding(0,12,0,12);.lh(32);border:1px solid rgba(7,17,27,.1);
          border-radius: 2px;background-color: #fff;.h(32);display: inline-block;
          .margin(0,16,0,0);.fs(18);color:rgb(147,153,159);.w(110);overflow: hidden;
          white-space: nowrap;text-overflow: ellipsis;}
        }
        .newstime{.fs(20);font-weight: 200;color:rgb(147,153,159);.lh(24);}
        .icon.icon-damuzhi{color:rgb(0,160,220);}

     }
  }
</style>



