<template>
  <div class="header">
    <div class="bg">
      <img :src="seller.avatar" alt="">
    </div>
    <div class="contentwrap">
        <img :src="seller.avatar" alt="">
        <div class="contentcenter">
            <h3><i></i>{{seller.name}}</h3>
            <p class="getTime">{{seller.description}}/{{seller.deliveryTime}}分钟送达</p>
            <p class="favorable"><i></i>{{seller.supports[0].description}},满50减10</p>
        </div>
      <div class="favorablenum" @click="show=true">
        <span>{{seller.supports.length}}</span>个 >
      </div>
    </div>
    <div class="notice" @click="show=true">
      <i></i><p>{{seller.bulletin}}</p><span>></span>
    </div>
    <transition name="bround">
      <noticemsg v-show="show" :seller="seller" @close="show=false"></noticemsg>
    </transition>
  </div>
</template>

<script>
import noticemsg from '../noticemsg/noticemsg'
export default {
  data:function(){
    return{
      show:false
    }
  },
  props:{
    seller:{
      type:Object,
      require:true
    }
  },
  components:{
    noticemsg
  }
}
</script>

<style lang="less" scoped>
  .header{
    box-sizing: border-box;
    height: 134px;
    width: 100%;
    background: rgba(7,17,27,.5);
    color: #fff;
    position: relative;
    .bg{
      position: absolute;top:0;
      left:0;bottom:0;right:0;z-index: -1;filter: blur(10px);
      img{
        width: 100%;height: 100%;
      }
    }
    .contentwrap{
      display: flex;
      justify-content: space-between;
        padding:24px 0 0 24px;
      img{
        width: 64px;
        height: 64px;
        border-radius: 4px;
      }
      .contentcenter{
        flex-grow: 1;margin-left: 16px;
        h3{
          i{
            width: 30px;
            height: 18px;
            display: inline-block;
            background: url('brand@2x.png') no-repeat;
            background-size: 30px 18px;vertical-align: top;margin-right: 6px;
          }
          font-size: 16px;font-weight: bold;line-height: 18px;
        }
        .getTime{
          font-size: 12px;font-weight: 200;line-height: 12px;
          margin-top: 8px;margin-bottom: 10px;
        }
        .favorable{
          font-size: 10px;font-weight: 200;line-height: 12px;
          i{
            display: inline-block;width: 12px;height: 12px;vertical-align: top;
            background: url('decrease_1@2x.png') no-repeat;
            background-size: 12px 12px;
          }
        }
      }
      .favorablenum{
        width: 50px;height: 24px;background-color: rgba(0,0,0,.2);
        border-radius: 50px;text-align: center;line-height: 24px;align-self: flex-end;
        font-size: 10px;font-weight: 200;margin-right: 12px;
      }
    }
    .notice{
      height: 28px;font-size: 10px;background-color: rgba(7,17,27,.2);
      display: flex;margin-top: 18px;align-items: center;
      i{
          display: inline-block;min-width: 22px;height: 12px;vertical-align: top;
          background: url('  bulletin@2x.png') no-repeat;
          background-size: 22px 12px;margin:0 4px 0 12px;
      }
      p{
        overflow: hidden;font-size: 10px;
        text-overflow: ellipsis;white-space: nowrap;line-height: 28px;
      }
      span{margin-right: 12px;margin-left: 4px;}
    }
  }
  @keyframes bround-in {
    0%{transform: scale(0);}
    50%{transform: scale(1.5);}
    100%{transform: scale(1);}
  }
  @keyframes bround-out {
    0%{transform: scale(1);}
    50%{transform: scale(1.5);}
    100%{transform: scale(0);}
  }
  .bround-enter-active{animation: bround-in 0.5s};
  .bround-leave-active{animation: bround-out 0.5s};
</style>



