<template>
  <div class="goods">
    <transition name="slide">
      <productDetails :fooddetail="fooddetail" v-show="open"
      @close="open = false"></productDetails>
    </transition>
    <div id="left-wrapper">
      <ul class="leftnav">
        <li ref="lilist" v-for="(item,index) in goods" @click="selectLi(index)" :class="{on:current==index}"><span>{{item.name}}</span></li>
      </ul>
    </div>
    <div id="right-wrapper">
      <div class="rightnav">
        <dl class="" v-for="item in goods" ref="dllist">
          <dt>{{item.name}}</dt>
          <dd v-for="food in item.foods"  @click="getcontent(food)">
              <img :src="food.icon" alt="">
              <div class="introduce">
                <h3>{{food.name}}</h3>
                <p v-if="food.description">{{food.description}}</p>
                <p>月售{{food.sellCount}}份　好评率{{food.rating}}</p>
                <p class="pirce"><span>¥</span><strong>{{food.price}}</strong>
                  <del v-if="food.oldPrice">¥{{food.oldPrice}}</del></p>
              </div>
              <addCart :food="food" @addNum="addNum(food)" @subNum="subNum(food)"></addCart>
          </dd>
        </dl>
      </div>
    </div>
    <ele-footer :cartgoods="cartgoods"
                :minPrice = "minPrice"
                :deliveryPrice="deliveryPrice"
    ></ele-footer>
  </div>
</template>

<script>
  require ('../../assets/js/iscroll-probe.js');
  import productDetails from '../productDetails/productDetails'
  import Footer from '../Footer/Footer'
  import addCart from '../addCart/addCart'
  export default {
    methods:{
      addNum(food){
        if(food.num){
          food.num++
        }else{
          this.$set(food,'num',1);
        }
      },
      subNum(food){
        food.num--;
      },
      getcontent(msg){
        this.fooddetail = msg;
        this.open = true;
        // console.log(this.fooddetail);
      },
      selectLi(ind){
        this.current = ind;
        let dlList = this.$refs.dllist;
        let liList = this.$refs.lilist;
        this.rightScroll.scrollToElement(dlList[ind]);
        // console.log(liList,dlList);
      }
    },
    data(){
      return{
        goods:[],
        current:0,//li当前是哪个变
        dlOffsettop:[],//所有dl的高度
        fooddetail:{},//详情页数据
        open:false,//是否显示详情页
        seller:{} //有配送信息
      }
    },
    created(){
      let ele = require('../../../data.json');
      this.goods = ele.goods;
      this.seller = ele.seller;
    },
    mounted(){
      this.leftScroll = new IScroll("#left-wrapper")
      this.rightScroll = new IScroll("#right-wrapper",{probeType:3});
      const _this = this;
      let dlList = _this.$refs.dllist;
      for(var i = 0;i<dlList.length;i++){
          _this.dlOffsettop.push(dlList[i].offsetTop)
      }
        // console.log(_this.dlOffsettop);
      this.rightScroll.on('scroll',function(){
        let y = Math.abs(this.y)
        // console.log(y)
          for(var i = 0;i<_this.dlOffsettop.length;i++){
            if(y>=_this.dlOffsettop[i]){
              _this.current = i;
            }
          }
          let liList = _this.$refs.lilist;
          _this.leftScroll.scrollToElement(dlList[_this.current]);
      })
    },
    components:{
      productDetails,
      addCart,
      "ele-footer":Footer
    },
    computed:{
      deliveryPrice(){
        return this.seller.deliveryPrice
      },
      minPrice(){
        return this.seller.minPrice
      },
      cartgoods(){
        let cartgoods = [];
        this.goods.forEach( item => {item.foods.forEach(food =>{
          if(food.num){
            cartgoods.push(food);
          }
        })
        })
          return cartgoods;
      }
    }
  }
</script>
<style lang="less" scoped>
  .slide-enter,.slide-leave-active{opacity:0;transform:translateX(100%);}
  .slide-enter-active,.slide-leave-active{transition:all .5s;}
  .goods{
    position: fixed;top:175px;bottom:48px;left:0;right:0;
    box-sizing: border-box;
    width: 100%;
    display: flex;overflow: hidden;
    .leftnav{
      min-width: 80px;
      li{height: 54px;padding:0 12px;  background-color: #f3f5f7;
        span{border-bottom: 1px solid rgba(7,17,27,.1);
          width: 100%;height: 100%;display: flex;justify-content: center;align-items: center;}
          &:last-child span{border-bottom: none;}
          &.on{background-color: #fff;}
        }
    }
    .rightnav{
      dt{height: 26px;padding-left:14px;font-size: 12px;  background-color: #f3f5f7;
        color:rgb(147,153,159);line-height:26px;border-left: 2px solid #d9dde1;}
      dd{img{width: 50px;height: 50px;margin-right: 10px;}
      font-size: 10px;color:rgb(147,153,159);position: relative;
      font-size: 10px;line-height: 10px;
        display: flex;padding:36px;background-color: #fff;
        border-bottom: 1px solid rgba(7,17,27,.1);
        .introduce{
          h3{font-size: 14px;line-height: 14px;color:rgba(7,17,27,1);}
          p{margin-top: 8px;}
          .pirce{color:red;font-size: 18px;line-height: 24px;font-weight: 700;
          span{font-size: 10px;line-height: 24px;}strong {font-size: 18px;line-height: 24px;font-weight: 700;}
          }
        }
        .addCart{position: absolute;bottom:18px;right:18px;}
      }
    }
  }
</style>



