<template >
  <div class="addCart">
    <transition name="rotate">
      <span class="icon icon-jian" v-if="food.num" @click.stop="subNum"></span>
    </transition>
    <transition name="fade">
        <strong v-show="food.num">{{food.num}}</strong>
    </transition>
    <span class="icon icon-jia" @click.stop="addNum"></span>
  </div>
</template>

<script>
export default {
  props:{
    food:{
      type:Object,
      require:true
    }
  },
  methods:{
    addNum(){
      this.$emit('addNum')
    },
    subNum(){
      this.$emit('subNum')
    }
  }
}
</script>

<style lang="less" scoped>
.rotate-enter,.rotate-leave-active{transform:translateX(48px) rotateZ(360deg) ;}
.rotate-enter-active,.rotate-leave-active{transition: all .5s;}
.fade-enter,.fade-leave-active{opacity: 0;}
.fade-enter-active,.fade-leave-active{transition: all .5s;}
.addCart{min-width: 72px;height: 24px;text-align: center;
  strong{margin:0 7px;font-size: 10px;line-height: 24px;}
  .icon-jian{position: absolute;top:5px;left:0px;
            color:rgb(0,160,220);font-size: 24px;}
  .icon-jia{color:rgb(0,160,220);font-size: 24px;position: absolute;;top:8px;right:0px;}
}
</style>



