<template >
    <div class="productDetails">
      <div >
        <div class="close" @click="close"><</div>
        <div class="productTop">
          <img :src="fooddetail.image" alt="">
          <div class="title">
            <h3>{{fooddetail.name}}</h3>
            <p>月售{{fooddetail.sellCount}}份　好评率{{fooddetail.rating}}</p>
            <p class="pirce">
              <span>¥</span><strong>{{fooddetail.price}}</strong>
              <del v-if="fooddetail.oldPrice">¥{{fooddetail.oldPrice}}</del>
              <i>加入购物车</i>
            </p>
          </div>
        </div>
        <div class="introduce">
          <h3>商品介绍</h3>
            <p>{{fooddetail.info}}</p>
        </div>
        <div class="evaluate">
          <h3>商品评价</h3>
          <div>
            <p class="all"><span>全部</span> {{allevaluate}}</p>
            <p class="recommend"><span>推荐</span>
              {{recommend}}
            </p>
            <p class="taunt"><span>吐槽</span>
              {{taunt}}
            </p>
        </div>
          <p class="select"><span class="icon icon-gou" @click="all=!all" :class="{only:!all}"></span>只看有内容的评价</p>
        </div>
        <ul class="evaluateMsg">
          <li v-for="(item,index) in showall">
            <div class="time">
              <span>{{time}}</span>
              <span>{{item.username}}<img :src="item.avatar" alt="" width="12" height="12">
              </span>
            </div>
              <div class="">
                <span class="icon" :class="{'icon-down':item.rateType == 0,'icon-damuzhi':item.rateType == 1}
                "></span><i>{{item.text}}</i>
              </div>
          </li>
        </ul>
      </div>
    </div>
</template>

<script>
  require ('../../assets/js/iscroll.js');
  export default {
  data(){
    return{
      all:true
    }
  },
  computed:{
    allevaluate(){
      if(this.fooddetail.ratings){
        return this.fooddetail.ratings.length;
      }else{
        return 3;
      }
    },
    recommend(){
      if(this.fooddetail.ratings){
        return this.fooddetail.ratings.filter( msg =>msg.rateType == 1).length;
      }else{
        return 1;
      }
    },
    taunt(){
      if(this.fooddetail.ratings){
        return this.fooddetail.ratings.filter( msg =>msg.rateType == 0).length;
      }else{
        return 2;
      }
    },
    time(){
      return new Date().toLocaleString();
    },
    showall(){
      if(this.all){
        return this.fooddetail.ratings
      }else{
        return this.fooddetail.ratings.filter(food => food.text);
      }
    }
  },
  props:{
    fooddetail:{
      type:Object,
      require:true
    }
  },
  methods:{
    close(){
      this.$emit('close');
    }
  },
  mounted(){
    new IScroll(".productDetails");
  },
  updated(){
    new IScroll(".productDetails");
  }
}
</script>

<style lang="less" scoped>

  .close{font-size: 30px;color:#fff;position:fixed;top:20px;left:10px;z-index: 3;}
  .productDetails{
    position: fixed;bottom:48px;left:0;right:0;background-color: #f3f5f7;top:0;z-index: 2;
    .productTop{background-color: #fff;border-bottom: 1px solid rgba(7,17,27,.1);
      img{width: 100%;}
      .title{padding:18px;color: rgb(174,153,159);
        h3{
          font-size: 14px;line-height: 14px;color:rgba(7,17,27,1);
        }
        p{margin-top: 8px;}
        .pirce{color:red;font-size: 18px;line-height: 24px;font-weight: 700;
                display: flex;
          span{font-size: 10px;line-height: 24px;}
          strong {font-size: 18px;line-height: 24px;font-weight: 700;}
          i{width: 74px;height: 24px;margin-left: auto;font-size: 10px;color:#fff;
            text-align:center;
            background-color: rgb(0,160,220);border-radius: 24px;}
        }
      }
    }
    .introduce{padding:18px;border-bottom:1px solid rgba(7,17,27,.1);
        border-top:1px solid rgba(7,17,27,.1);margin-top:16px;background-color: #fff;
        p{line-height: 24px;}
    }
    .evaluate{
      padding:18px;border-bottom:1px solid rgba(7,17,27,.1);
      border-top:1px solid rgba(7,17,27,.1);margin-top:16px;background-color: #fff;
      div{border-bottom:1px solid rgba(7,17,27,.1); padding-bottom: 18px;padding-top: 18px;}
      .all{height: 29px;width: 50px;color:#fff;line-height: 29px;text-align: center;
        background-color: rgb(0,160,220);display: inline-block;font-size: 8px;
        vertical-align: middle;
        span{font-size: 12px}
      }
        .recommend{height: 29px;width: 50px;color:rgb(77,85,93);line-height: 29px;text-align: center;
          background-color: rgba(0,160,220,.2);display: inline-block;font-size: 8px;
          span{font-size: 12px}
        }
          .taunt{height: 29px;width: 50px;color:rgb(77,85,93);line-height: 29px;text-align: center;
                background-color: rgba(77,85,93,.2);display: inline-block;font-size: 8px;
                span{font-size: 12px}
            }
        .select{margin-top: 12px;color:rgb(147,153,159);
          .icon{margin-right: 4px;} .icon.only{color:rgb(0,160,220);}
        }
    }
    .evaluateMsg{
      padding:0 18px;background-color: #fff;
      li{border-bottom:1px solid rgba(7,17,27,.1);
        img{display: inline-block;margin-left: 6px;}
          .time{display: flex;justify-content: space-between;margin-bottom: 12px;}
          .icon.icon-damuzhi{color:rgb(0,160,220);}
       }
    }
  }
</style>



