<template >
  <div class="Seller">
    <div class="">
      <div class="sellerTop">
        <div class="title">
          <div class="title-left">
            <h3>{{seller.name}}</h3>
            <Start :seller="seller.deliveryPrice" class="comments"></Start>
            <span>(661)</span><em>月售{{seller.sellCount}}单</em>
          </div>
          <div class="">
            <span class="icon icon-aixin" style="color:red;"></span>
            <p>已收藏</p>
          </div>
        </div>
          <ul class="delivery">
            <li><p>起送价</p><strong>{{seller.minPrice}}</strong>元</li>
            <li><p>商家配送</p><strong>{{seller.deliveryPrice}}</strong>元</li>
            <li><p>平均配送时间</p><strong>{{seller.deliveryTime}}</strong>分钟</li>
          </ul>
      </div>
      <div class="activity">
          <h3>公告与活动</h3>
          <p>{{seller.bulletin}}</p>
          <ul>
            <li v-for="msg in seller.supports">
              <span>{{msg.type}}</span>{{msg.description}}
            </li>
          </ul>
      </div>
      <div class="live-action">
        <h2>商家实景</h2>
        <div class="slide">
          <ul>
            <li v-for="pic in seller.pics">
              <img :src="pic" alt="">
            </li>
          </ul>
        </div>

      </div>
      <div class="sellermsg">
        <h2>商家信息</h2>
        <ul>
          <li v-for="msg in seller.infos">{{msg}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import ele from '../../../data.json';
  import Start from '../start/start';
  require ('../../assets/js/iscroll.js');
  export default {
    data(){
      return {
        seller:{}
      }
    },
    created(){
      this.seller = ele.seller;
    },
    components:{
      Start
    },
    mounted(){
      new IScroll(".slide",{
        scrollX:true,
        scrollY:false
      });
      new IScroll('.Seller')
    }
  }
</script>

<style lang="less">
    @import '../../assets/less/public.less';
    .Seller{background-color:rgba(7,17,27,.1);position: fixed;top:175px;bottom:0;left:0;right:0;
    box-sizing: border-box;width: 100%;overflow: hidden;}
    .sellerTop{
      .padding(36,36,36,36); border-bottom: 1px solid rgba(7,17,27,.1);
      background-color: #fff;
      .title{
        display: flex;justify-content: space-between;align-items: center;
        border-bottom: 1px solid rgba(7,17,27,.1);.padding(0,0,36,0);
        .title-left{
          span,em{.lh(36);.fs(20);color:rgb(77,85,93);}
          span{.margin(0,24,0,0);}
        }
        h3{.fs(28);color:rgb(7,17,27);.lh(28);.margin(0,0,16,0);}
        div:last-child{text-align: center}
        .comments{display: inline-block;.margin(0,16,0,0);}
      }
      .delivery{
        display: flex;align-items: center;.padding(36,0,0,0);
        li{
          flex:1;text-align: center;.fs(20);border-right: 1px solid rgba(7,17,27,.1);
          &:last-child{border-right: none;}
          p{color:rgb(147,153,159);.lh(20);.margin(0,0,8,0);}
          strong{.fs(48);font-weight: 200;color:rgb(7,17,27);.lh(48);}
        }
      }
    }
    .activity{
      .padding(36,36,0,36);background-color: #fff;.margin(36,0,0,0);
      h3{.margin(0,0,16,0);}
      p{.padding(0,24,0,24);.fs(24);font-weight: 200;color:rgb(240,20,20);
      .lh(48);.margin(0,0,36,0);}
      li{border-top: 1px solid rgba(7,17,27,.1);.h(32);
        .padding(32,0,32,0);.fs(24);font-weight: 200;color:rgb(7,17,27);.lh(32);
        span{
          .h(32);.w(32);.margin(0,12,0,0);
        }
      }
    }
    .live-action{
      .padding(36,0,36,36);.margin(32,0,0,0);background-color: #fff;
      border-bottom: 1px solid rgba(7,17,27,.1);
      .slide{width: 100%;.margin(24,0,0,0);overflow-x:  hidden;
        ul{width: 750px;
          li{float:left;.margin(0,12,0,0);}}
      }
    }
    .sellermsg{
      background-color: #fff;.padding(36,0,36,36);.margin(32,0,0,0);
      h2{.margin(0,0,24,0);}
      li{border-top: 1px solid rgba(7,17,27,.1);.h(32);
        .padding(32,0,32,24);.fs(24);font-weight: 200;color:rgb(7,17,27);.lh(32);}
    }
</style>



