<template lang="html">
    <div class="noticemsg">
        <h3>{{seller.name}}</h3>
        <Start :seller="seller.deliveryPrice" class="notice"></Start>
        <h4 class="msg">优惠信息<p></p></h4>
        <p v-for="msg in seller.supports" class="msglist">{{msg.description}}</p>
        <h4 class="noticer"><span>商家公告</span></h4>
        <p class="notice">{{seller.bulletin}}</p>
        <div class="close" @click="close">×</div>
  </div>

</template>

<script>
  import Start from '../start/start'
export default {
  components:{
    Start
  },
  props:{
    seller:{
      type:Object,
      require:true
    }
  },
  methods:{
    close(){
      this.$emit('close')
    }
  }
}
</script>

<style lang="less" scoped>
  .noticemsg{
    position: fixed;
    z-index: 5;
    top:0px;
    bottom:0px;
    left:0;right:0;
    background-color: rgba(7,17,27,.8);
    padding:0 36px;
    color:#fff;
    h3{
      margin-top: 64px;font-size: 16px;font-weight: 700;
      text-align: center;margin-bottom: 16px;
    }
    h4.msg{
      font-size: 14px;font-weight: 700;line-height: 28px;position: relative;
      text-align: center;
    }
    .msglist{
      margin:12px;
    }
    .noticer{
      font-size: 14px;font-weight: 700;line-height: 28px;position: relative;
      text-align: center;margin:28px 0 24px 0;
    }
    .notice{
      font-size: 12px;font-weight: 200;
    }
    .close{
      font-size: 32px;position:fixed;bottom:32px;left:50%;transform: translate(-50%);
    }
  }
</style>



