<template >
  <div class="s">
    <ul class="silverstart">
      <li><img src="./star24_off@2x.png" alt=""></li>
      <li><img src="./star24_off@2x.png" alt=""></li>
      <li><img src="./star24_off@2x.png" alt=""></li>
      <li><img src="./star24_off@2x.png" alt=""></li>
      <li><img src="./star24_off@2x.png" alt=""></li>
    </ul>
    <ul class="goldenstart">
      <li v-for="start in goldenstart"><img src="./star24_on@2x.png" alt=""></li>
    </ul>
    <ul class="halfstart">
      <li v-for="start in halfstart"><img src="./star24_half@2x.png" alt=""></li>
    </ul>
  </div>
</template>

<script>
export default {
  props:{
    seller:{type:Number,require:true}
  },
  computed:{
    goldenstart(){
      return Math.floor( this.seller)
    },
    halfstart(){
        return Math.round( this.seller)
    }
  }
}
</script>

<style lang="less">
  .notice{
    .goldenstart{
      overflow: hidden;margin-left: 50px;
      height: 24px;position: relative;vertical-align: middle;
      top:-24px;z-index: 2;
      li{
        float:left;margin:0 10px;
      }
    }
    .halfstart{
      overflow: hidden;margin-left: 50px;
      height: 24px;position: relative;vertical-align: middle;
      top:-48px;z-index: 1;
      li{
        float:left;margin:0 10px;
      }
    }
    .silverstart{
      overflow: hidden;margin-left: 50px;
      height: 24px;vertical-align: middle;
      li{
        float:left;margin:0 10px;
      }
    }
  }
  .comments{
    img{width: 18px;height: 18px;}position: relative;
    .goldenstart{
      overflow: hidden;
      position: absolute;vertical-align: middle;display: flex;top:0;left:0;
      z-index: 2;
      li{
        float:left;margin:0 1px;
      }
    }
    .halfstart{
      overflow: hidden;
      height: 18px;position: absolute;vertical-align: middle;display: flex;
      z-index: 1;top:0;left:0;
      li{
        float:left;margin:0 1px;
      }
    }
    .silverstart{
      overflow: hidden;
      height:18px;vertical-align: middle;display: flex;
      li{
        float:left;margin:0 1px;
      }
    }
  }
  .usergrade{
      img{width: 10px;height: 10px;}position: relative;
      .goldenstart{
        overflow: hidden;
        position: absolute;vertical-align: middle;display: flex;top:0;left:0;
        z-index: 2;
        li{
          float:left;margin:0 1px;
        }
      }
      .halfstart{
        overflow: hidden;
        height: 18px;position: absolute;vertical-align: middle;display: flex;
        z-index: 1;top:0;left:0;
        li{
          float:left;margin:0 1px;
        }
      }
      .silverstart{
        overflow: hidden;
        height:10px;vertical-align: middle;display: flex;
        li{
          float:left;margin:0 1px;
        }
      }
  }
  // .goldenstart{
  //   overflow: hidden;margin-left: 50px;
  //   height: 24px;position: relative;vertical-align: middle;
  //   top:-24px;z-index: 2;
  //   li{
